.startPage-main{
    padding: 3em 0;
    max-width: calc(100%-6em);
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: opacity 1s ease-in-out;
    background: var(--startPage-background-color,#FFF6F3);
}
form{
    color:red;
    font-size:3em;
    display:flex;
    align-items:center;
    justify-content:center;
}
